.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  /* Height of the footer*/ 
  height: 55px;
  background-color: rgba(150, 219, 148, .3);
}


.footer-text {
  color: var(--strawberry);
  text-align: right;
}

.footer-icon {
  text-align: left;
  margin: 0;
  padding-left: 20px;
  color: var(--strawberry);
  display: inline;
}