p {
   text-align: center;
   margin-bottom: 10px;
   color: var(--strawberry);
   font-size: 20px;
}

.fa-envelope-open-text {
   margin-right: 10px;
}

.fa-mobile-alt {
   margin-right: 10px;
}

form {
   margin-top: 50px;
   border: 1px solid var(--kiwi);
   padding: 30px;
   border-radius: 20px;
   box-shadow: 10px 10px 4px 1px rgba(131, 184, 129, .3);
}

.btn {
   color: var(--dark);
   background-color: rgba(238, 109, 137, .3);
   border: none;
   border-radius: 20px;
   width: 30%;
   margin-left: 35%;
   margin-top: 20px;
}

.btn:hover{
   background-color: var(--strawberry);
   color: var(--dark);
   font-weight: bold;
}